h1 {
  text-align: center;
  margin-bottom: 1rem;
  color: var(--yellow);
}

.mails-container {
  max-width: 900px;
  margin: 0 auto;
}
.mails-container > ul {
  list-style: none;
  padding: 0;
}
.mails-container > ul > li {
  background-color: #333;
  border: 1px solid #555;
  border-radius: 8px;
  padding: 1.2rem;
  margin-bottom: 1rem;
  transition: background-color 0.3s, border-color 0.3s;
}
.mails-container > ul > li:hover {
  background-color: #3a3a3a;
  border-color: #666;
}
.mails-container > ul > li h3 {
  font-size: 1.3rem;
  color: var(--txt);
  font-weight: bold;
}
.mails-container > ul > li p {
  text-align: left;
}
.mails-container > ul > li button {
  background-color: var(--yellow);
  color: #1a1a1a;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
  margin-top: 1rem;
}
.mails-container > ul > li button:hover {
  background-color: #ffc107;
}

p {
  text-align: center;
  font-style: italic;
  color: #aaa;
}

.files-container {
  margin-top: 1rem;
  border-top: 1px solid #555;
  display: none;
}
.files-container ul {
  list-style: none;
}
.files-container li {
  padding: 0.4rem 0;
}
.files-container li a {
  color: var(--txt);
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 0.4rem 0.8rem;
  border-radius: 4px;
  transition: background-color 0.3s, color 0.3s;
}
.files-container li a .file-icon {
  margin-right: 10px;
  font-size: 1.2em;
}
.files-container li a:hover {
  background-color: #444;
  color: var(--yellow);
}

.file-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}
.file-popup .popup-content {
  background-color: #2c2c2c;
  color: var(--txt);
  padding: 15px;
  border-radius: 8px;
  overflow: auto;
  position: relative;
  border: 1px solid var(--yellow);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}
.file-popup .popup-content .popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #555;
  margin-bottom: 15px;
  padding-bottom: 10px;
}
.file-popup .popup-content .popup-header h3 {
  margin: 0;
  font-size: 1.4rem;
  color: var(--yellow);
  margin-right: 1rem;
}
.file-popup .popup-content .popup-header .close-popup {
  background: none;
  border: none;
  font-size: 28px;
  cursor: pointer;
  color: #aaa;
  transition: color 0.3s;
}
.file-popup .popup-content .popup-header .close-popup:hover {
  color: var(--yellow);
}
.file-popup .popup-content img,
.file-popup .popup-content video {
  max-width: 80vw;
  max-height: 80vh;
  display: block;
  border-radius: 4px;
  margin: 10px auto;
}
.file-popup .popup-content audio {
  display: block;
  margin: 10px auto;
  max-width: 100%;
}
.file-popup .popup-content iframe {
  width: 80vw;
  height: 80vh;
  max-width: 100%;
  display: block;
  border-radius: 4px;
  margin-top: 10px;
  border: none;
}
.file-popup .popup-content pre {
  background-color: #1e1e1e;
  color: #d4d4d4;
  padding: 1rem;
  border-radius: 4px;
  white-space: pre-wrap;
  word-break: break-all;
  max-height: 70vh;
  overflow: auto;
}
.file-popup .popup-content a {
  color: var(--yellow);
  text-decoration: none;
}
.file-popup .popup-content a:hover {
  text-decoration: underline;
}